import React from 'react'
import uddh5 from '../../common/js/common.js'

var XingCheng=React.createClass({
  toggleNative: function(){
    //app
     var requestHybrid={
        tagname: 'forward',
        topage:'detail',
        type:"native",
        param: {
          typeId: "10117",
          text: "898888 05-2605-21@ ",
          url: "http://m.uddctrip.com/app/"
        }
     }
     var native_callback=function(data){
          
     }
     uddh5.bridge(native_callback,requestHybrid);
  },
  render: function(){
    return (
      <div className="trip-share">
        <h2></h2>
        <ul>
          <li className="item">
            <p>898888 05-2605-21@ http://m.uddctrip.com/app/</p>
            <div className="absolute-middle share-btn">
              <a href="javascript:void(0)" onClick={this.toggleNative}></a>
            </div>
          </li>
          <li className="item">
            <p>898888 05-2605-21@ http://m.uddctrip.com/app/</p>
            <div className="absolute-middle share-btn">
              <a href="javascript:void(0)" onClick={this.toggleNative}></a>
            </div>
          </li>
        </ul>
      </div>
    );
  }
});

